.SearchResults {
    width: 50%;
    height: 800px;
    overflow-y: scroll;
    padding: .88rem;
    background-color: #430095a0;
    border-radius: 20px;
    
    box-shadow: 0 4px 2px 2px #000000;
  }
  
  ::-webkit-scrollbar {
      width: 0px;
      background: transparent;
  }
  
  @media only screen and (max-width: 1020px) {
    .SearchResults {
      width: 90%;
      margin-bottom: 2rem;
    }
  }